  }
  .university-image {
    margin-top: 3em;
    margin-bottom: 3em;
  }
  .img-thumbnail {
    width: 70%;
    height: 20%;
    margin-bottom: 1em;
  }
  .boi {
    margin-top: 30%;
  }
  
</style>



